



//  Link Map 

import { Link, LinksData, NavigationLinkNames } from "../types/base";


// 



export const urlMap = new Map<NavigationLinkNames, string>([
    ["appendices", "/appendices"],
    ["projects", "/projects"],
    ["events", "/events"],
    ["community", "/community"],
    ["join", "/join"],
    ["sanology", "/"]
]);


//  Dictionaries 


export type Dictionaries = LinksData;

export const he: Dictionaries = {

    appendices: "",
    projects: "",
    events: "",
    community: "",
    join: "",
    sanology: "",

    // other: {
    //     about: "",
    //     contact: " ",
    //     home: "",


    // }
}



function getDictionary() {
    //TODO: add other languages and refactor getDictionaries
    return he;

}





export const getNavLinks = () => {


    const dict = getDictionary();

    const keys = Object.keys(he) as NavigationLinkNames[];

    return keys.map(key => {

        return { text: dict[key], url: urlMap.get(key) } as Link;


    });
}